import React, { Fragment } from 'react'
import styled from 'styled-components'
import axios from 'axios'
import Head from 'next/head'
import moment from 'moment'
import Main from '../layouts/main'

const SpecialityWrapper = styled.div`
    max-width: 760px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    background: #fff;
`

const Especialidade = props => {
    console.log(props)
    const { title, author, publishedAt } = props.speciality
    return (
        <Fragment>
            <Head>
                <title>Orto Conecta | {props.speciality.title}</title>
            </Head>
            <Main>
                <SpecialityWrapper>
                    <h1>{title}</h1>
                    <h2>{author}</h2>
                    <h3>{
                        moment.locale('pt-br'),
                        moment(publishedAt).format("LL")
                    }</h3>
                </SpecialityWrapper>
            </Main>
        </Fragment>
    );
}

Especialidade.getInitialProps = async ({ query }) => {
    const response = await axios.get(`http://ortoconecta-plataforma-brunogcpinheiro.c9users.io:8080/specialities/${query.id}`)
    
    return { speciality: response.data }
}

export default Especialidade


